export const translateState = (state) => {
  switch (state) {
    case "open":
    case "OPEN":
      return "ouvert";
    case "new":
    case "NEW":
      return "nouveau";
    case "closed":
    case "CLOSED":
      return "archiv";
    case "pending reminder":
    case "PENDING REMINDER":
      return "rappel en attente";
    case "pending closure":
    case "PENDING CLOSURE":
      return "clture en attente";
    case "pending":
    case "PENDING":
      return "en attente";
    default:
      return state;
  }
};

export const translateCohort = (cohort) => {
  switch (cohort) {
    case "Fvrier 2022":
      return "du 13 au 25 Fvrier 2022";
    case "Juin 2022":
      return "du 12 au 24 Juin 2022";
    case "Juillet 2022":
      return "du 3 au 15 Juillet 2022";
    default:
      return cohort;
  }
};

